<template>
<div>

    <br>
    <gmap-map :center="center" :zoom="12" style="width:100%;  height: 400px;">
        <gmap-marker :key="index" v-for="(m, index) in markers" :position="m.position" @click="center=m.position"></gmap-marker>
    </gmap-map>
</div>
</template>

<script>
import Vue from "vue";

Vue.use(VueGoogleMaps, {
    load: {
        key: '',
    },
    installComponents: true,

})

Vue.component('google-map', VueGoogleMaps.Map);
import * as VueGoogleMaps from 'vue2-google-maps'
import {
    gmapMap,
    gmapMarker,
    gmapCluster,
    gmapInfoWindow,
    loaded
} from 'vue2-google-maps'
export default {
    name: "GoogleMap",
 
    data() {
        return {
            //mặc định là Montreal
            center: {
                lat: 10.761011199999999 ,
                lng: 10.761011199999999
            },
            markers: [],
            places: [],
            currentPlace: null
        };
    },

    mounted() {
        this.geolocate();
    },

    methods: {
        // nhận địa điểm thông qua autocomplete component
        setPlace(place) {
            this.currentPlace = place;
        },
        addMarker() {
            if (this.currentPlace) {
                const marker = {
                    lat: this.currentPlace.geometry.location.lat(),
                    lng: this.currentPlace.geometry.location.lng()
                };
                this.markers.push({
                    position: marker
                });
                this.places.push(this.currentPlace);
                this.center = marker;
                this.currentPlace = null;
            }
        },
        geolocate: function () {
            navigator.geolocation.getCurrentPosition(position => {
                this.center = {
                    lat: position.coords.latitude,
                    lng: position.coords.longitude
                };
            });
        }
    }
};
</script>



