            let resource = this.form.resource
            let gia = this.form.gia
            let danhmuc = this.form.danhmuc
            let checkList = this.form.checkboxGroup1

            var j = 0
            var checkedValue = [];
            var inputElements = document.getElementsByName('interest');
            for (var i = 0; inputElements[i]; ++i) {
                if (inputElements[i].checked) {
                    checkedValue[j] = inputElements[i].value;
                    j++
                }
            }
            this.form.checkboxGroup1 = checkedValue
            console.log('resource, gia, danhmuc, this.form.checkboxGroup1 : ', resource, gia, danhmuc, this.form.checkboxGroup1)
            this.handleSubmit(this.form) && this.handleSubmit({
