    color: #fff;
}

.social-icon .tooltip {
    visibility: visible;
    opacity: 1;
    transform: translate(-50%, -150%);
}

.tooltip :active {
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5) inset;
}

i {
    position: relative;
    top: 1px;
}

/* Tooltips */
.tooltip {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    padding: 0.4rem 0.6rem;
    border-radius: 40px;
    font-size: 0.4rem;
    font-weight: bold;
    opacity: 0;
    pointer-events: none;
    text-transform: uppercase;
    transform: translate(-50%, -100%);
    transition: all 0.3s ease;
    z-index: 1;
}

.tooltip:after {
    display: block;
    position: absolute;
    bottom: 1px;
    left: 50%;
    width: 0;
    height: 0;
    content: "";
    border: solid;
    border-width: 10px 10px 0 10px;
    border-color: transparent;
    transform: translate(-50%, 100%);
}

.el-form-item__label {
    text-align: right;
    vertical-align: middle;
    float: left;
    font-size: 14px;
    color: #606266;
    line-height: 40px;
    padding: 0 12px 0 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

@media only screen and (max-width: 480px) and (min-width: 320px) {
    .el-radio {
        margin: 1px;
    }

    .el-radio__label {
        font-size: 12px;
        padding-left: 4px;
    }

    .el-radio--medium.is-bordered {
        padding: 5px;
        border-radius: 4px;
        height: 27px;
    }

    .el-checkbox-group ul {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        border-radius: 10px;
        padding: 3px;
    }

    .el-checkbox-group ul li {
        position: relative;
        list-style: none;
        text-align: center;
        margin: 0px 2px;
        height: 90px;
        text-align: center;
    }

    .el-form-item {
        margin-bottom: 0px;
    }

    ul li .icon-box {
        width: 35px;
        height: 35px;
    }

    .el-radio__label {
        font-size: 14px;
        padding-left: 4px;
    }

    .el-radio,
    .el-radio--medium.is-bordered .el-radio__label {
        font-size: 11px;
        width: 100%;
    }

    .dialog-container {
        max-width: 555px;
        -webkit-box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
        box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        padding: 10px;
        background-color: #fff;
    }

    .style-name-check {
        font-size: 10px;
        width: 53px;
        line-height: 14px;
    }

    .el-slider__runway.show-input {
        margin-right: 140px;
        width: auto;
    }

    .el-form-item__label {
        text-align: right;
        vertical-align: middle;
        float: left;
        font-size: 12px !important;
        color: #606266;
        line-height: 40px;
        padding: 0 10px 0 0 !important;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }

    .el-radio.is-bordered+.el-radio.is-bordered {
        margin-left: 0px !important;
    }

    .dialog-title {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 18px;
        font-family: "Open Sans";
    }
}
</style>



