<template>
<div id="__next">

    <Header />
    <div class="f1e5lflt _2py_lf">
        <div class="_1aU_RW">

            <div>

                <div id="map"></div>

                <!-- <div id="map"></div> -->
                <!-- <GoogleMap /> -->
            </div>

        </div>
    </div>
    <Footers />
    <Footer />
</div>
</template>

<script>
import Header from '@/components/ComponentsPartial/Header'
import Footer from '@/components/ComponentsPartial/Footer'
import Footers from '@/components/Home/Footer'
import GoogleMap from '@/components/Map/GoogleMap'
export default {

    path: '/maps',
    name: "Maps",
    components: {
        Header,
        Footer,
        GoogleMap,
        Footers

    },
    data() {
        return {

        }
    },
    methods: {

    },
    updated() {
        this.initMap()
        // Initialize and add the map
        // const labels = "AIzaSyBauszIOMx_IbXO8b5oUM3BotI0P-j5m7o";
        // let labelIndex = 0;

        // function initMap() {
        //     const bangalore = {
        //          lat: 10.761011199999999 ,
        //         lng: 10.761011199999999
        //     };
        //     const map = new google.maps.Map(document.getElementById("map"), {
        //         zoom: 12,
        //         center: bangalore,
        //     });
        //     // This event listener calls addMarker() when the map is clicked.
        //     google.maps.event.addListener(map, "click", (event) => {
        //         addMarker(event.latLng, map);
        //     });
        //     // Add a marker at the center of the map.
        //     addMarker(bangalore, map);
        // }

        // // Adds a marker to the map.
        // function addMarker(location, map) {
        //     // Add the marker at the clicked location, and add the next-available label
        //     // from the array of alphabetical characters.
        //     new google.maps.Marker({
        //         position: location,
        //         label: labels[labelIndex++ % labels.length],
        //         map: map,
        //     });
        // }

    },
    methods: {
        initMap() {
            var lat_lng = {
                lat: 10.7571534,
                lng: 106.68809549999999
            };
            var directionsService = new google.maps.DirectionsService;
            var directionsDisplay = new google.maps.DirectionsRenderer;
            var map = new google.maps.Map(document.getElementById('map'), {
                zoom: 6,
                center: lat_lng
            });
            directionsDisplay.setMap(map);

            var onChangeHandler = function () {
                calculateAndDisplayRoute(directionsService, directionsDisplay);
            };
            document.getElementById('Source').addEventListener('change', onChangeHandler);
            document.getElementById('Destination').addEventListener('change', onChangeHandler);
        },

        calculateAndDisplayRoute(directionsService, directionsDisplay) {
            directionsService.route({
                origin: document.getElementById('Source').value,
                destination: document.getElementById('Destination').value,
                travelMode: google.maps.TravelMode.DRIVING
            }, function (response, status) {
                if (status === google.maps.DirectionsStatus.OK) {
                    directionsDisplay.setDirections(response);
                } else {
                    window.alert('Request for getting direction is failed due to ' + status);
                }
            });
        }
    },

};
</script>

<style scoped>
#map {
