<template>
<div class="_1aU_RW">
    <div class="_1_zoI4 ">
        <div class="_3rf2nn">
            <transition name="dialog">
                <div v-if="active" class="dialog-backdrop" @click="handleBackdropClick">
                    <div class="dialog-container" @click.stop>
                        <slot />
                    </div>
                </div>
            </transition>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "BaseFilter",
    props: {
        active: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        handleBackdropClick() {
            this.$emit("update:active", false);
        }
    }
};
</script>

<style scoped>
.dialog-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
}

@media only screen and (max-width: 480px) and (min-width: 320px) {

    .dialog-container {
        max-width: 555px;
        box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        padding: 10px;
        background-color: #fff;
    }

    .el-form-item__label {
        text-align: right;
        vertical-align: middle;
        float: left;
        font-size: 12px;
        color: #606266;
        line-height: 40px;
        padding: 0 10px 0 0;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }

    .el-radio.is-bordered+.el-radio.is-bordered {
        margin-left: 0px !important;
    }

    .dialog-title {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 18px;
        font-family: "Open Sans";
    }
}

.dialog-container {
    max-width: 555px;
    box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    padding: 15px;
    background-color: #fff;
}

.dialog-enter-active,
.dialog-leave-active {
    transition: opacity 0.2s;
}

.dialog-enter,
.dialog-leave-to {
    opacity: 0;
}

.el-input__inner {
    height: 30px;
}

.dialog-enter-active .dialog-container,
.dialog-leave-active .dialog-container {
    transition: transform 0.4s;
}

.dialog-enter .dialog-container,
.dialog-leave-to .dialog-container {
    transform: scale(0.9);
}
</style>



